import { buttons, specialClasses } from "./data.js";

const generateBtns = (container) => {
  buttons.forEach((button) => {
    const btn = document.createElement("button");
    btn.innerHTML = button;

    const specialClass = specialClasses[button];
    specialClass && btn.classList.add(specialClass);

    container.appendChild(btn);
  });
};

export default generateBtns;
